<template>
    <div class="row">
        <div class="col-lg-24 error-page">
            <section-block title="Страница не найдена">
                <div class="error">Страница не найдена</div>
            </section-block>
        </div>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    export default {
        name: 'error404',
        components: {
            sectionBlock
        }
    }
</script>

<style lang="scss">
    .error-page div.error {
        font-size: 20px;
        text-align: center;
        font-size: 40px;
        height: calc(100vh - 150px);
        line-height: calc(100vh - 150px);

        span {
            font-size: 160px;
        }
    }
</style>


